import React from 'react'

import styles from './SearchForm.module.scss'

const SearchForm = () => {
  return (
    <div className={styles.searchForm}>
        <h3 className={styles.title}>  ,  ?</h3>
        <p className={styles.text}>        30 !</p>
        <form action="#" className={styles.form}>
            <input type="text" placeholder=' ' className={styles.input} />
            <input type="tel" placeholder=' ' className={styles.input} />
            <textarea placeholder='   ()' className={styles.textarea}></textarea>
            <button type='button' className={styles.button}></button>
        </form>
    </div>
  )
}

export default SearchForm