import React from 'react'
import IconCreator from '../../icons/IconCreator'

import styles from './Footer.module.scss'

const Footer = () => {
  return (
    <footer className={styles.footer}>
        <div className={styles.company}>
            <img src="img/logo.png" alt="" className={styles.logo} />
            <button type='button' className={styles.button}> </button>
        </div>
        <ul className={styles.nav}>
            <li className={styles.navItem}></li>
            <li className={styles.navItem}></li>
            <li className={styles.navItem}></li>
            <li className={styles.navItem}> </li>
            <li className={styles.navItem}> </li>

        </ul>
        <ul className={styles.nav}>
            <li className={styles.navItem}>  </li>
            <li className={styles.navItem}>3D- </li>
            <li className={styles.navItem}> </li>
            <li className={styles.navItem}></li>
            <li className={styles.navItem}></li>
        </ul>
        <div className={styles.info}>
            <div className={styles.subscribe}>    .. !</div>
            <div className={styles.socials}>
                <IconCreator type='vk' />
                <IconCreator type='inst' />
                <IconCreator type='fb' />
                <IconCreator type='yt' />
            </div>
            <div>   :</div>
            <img src="img/logo-white.png" alt="" />
        </div>
    </footer>
  )
}

export default Footer